<template>
  <div class="gocar">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow @click-left="back" />

    <div class="order-box">
      <div class="address" @click="openPopup">{{userAddress.address}}</div>

      <div class="order-content">
        <div class="order-item">
          <div class="list">
            <van-swipe-cell class="list-item" v-for="(item,index) in shop" :key="index">
              <div class="content">
                <div class="content-img">
                  <img class="auto-img" :src="item.img" alt />
                </div>
                <div class="content-message">
                  <p class="text-o">{{item.title}}</p>
                  <p class="text-n">{{item.style}}/{{item.size}}</p>

                  <div class="product-price">
                    <div class="price fl">￥{{item.price}}</div>
                    <div class="fr product-count">×{{item.count}}</div>
                  </div>
                </div>
              </div>
            </van-swipe-cell>
          </div>

          <div class="order-total clearfix">
            <div class="fr clearfix">
              <span class="fl total-count">共计 {{counts}} 件商品 合计：</span>
              <span class="fl total-price">￥{{total}}.00</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-submit-bar :price="total * 100" button-text="立即结算" button-type="info" @submit="commit" />

    <van-popup class="popup-box" v-model="isOpen" position="bottom">
      <van-address-list
       @add="addAddress"
        v-model="aid"
        :list="addressList"
        default-tag-text="默认"
        @select="selectAddress"
      />
    </van-popup>
  </div>
</template>

<script>
//命名空间用法
import { createNamespacedHelpers } from "vuex";
const {mapState, mapMutations } = createNamespacedHelpers("shopcart");
export default {
  name: "Gocar",
  data() {
    return {
      isOpen: false,
      aid: "",
      addressList: [],
      userAddress: {
        address: "请选择收货地址",
        phone: "",
        receiver: ""
      },
      shopping: [],
      //商品总数量
      counts: 0,

      //总金额
      total: 0
    };
  },

  created() {
    //截取查询参数sids
    // 
    
    
    this.shopping = this.$route.query.shopping;
    if(this.shopping[0].constructor == Object){
      
      this.addshop(this.shopping)
    }
    
    
    this.shop.forEach(v => {
      this.counts += v.count;
      this.total += v.count * v.price;
    });
    this.shopping = this.shop

    this.getAddressData();
  },
  computed:{
    ...mapState(["shopnum","shop"])
  },

  methods: {
    ...mapMutations(["addDingdan","deleteshop","addshop","deleteshop"]),
    //弹出地址选择
    openPopup() {
      this.isOpen = true;
    },
    //返回
    back() {
      this.$router.go(-1);
    },

    //获取地址数据
    getAddressData() {
      let tokenString = localStorage.getItem("_TNK");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 20000) {
            result.data.result.forEach(v => {
              let currentAddress = {
                id: v.aid,
                tel: v.tel,
                name: v.name,
                address: v.province + v.city + v.county + v.addressDetail,
                isDefault: Boolean(v.isDefault)
              };

              //默认选择默认地址
              if (v.isDefault) {
                this.aid = v.aid;

                //如果存在默认地址，则默认选择默认地址
                this.userAddress.address = currentAddress.address;
              }

              this.addressList.push(currentAddress);
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //选择地址
    selectAddress(item) {
      
      this.isOpen = false;
      this.address = item.address;

      this.userAddress = {
        address: item.address,
        phone: item.tel,
        receiver: item.name
      };
    },

    //新增地址
    addAddress() {
      this.$router.push({ name: "Address" });
    },

    //提交  交钱
    commit() {
      if (this.userAddress.address == "请选择收货地址") {
        this.$toast("请选择收货地址");
        this.$router.push({name:"Address"})
        return;
      }

      //结算成功后   跳到订单页面
      this.shopping.forEach(v=>{
        
          if(v.isActive){
            this.deleteshop(v)
          }
      })

      this.addDingdan(this.shopping);

      this.deleteshop()

      this.$router.push({ name: "Order" });
      // this.shopping.forEach(){

      // }   
      

      
      

    }
  }
};
</script>
<style lang="less" scoped>
.gocar {
  .order-item {
    border-radius: 10px;
    background-color: #fff;
  }
  .list {
    max-height: calc(~"100vh - 200px");
    overflow: hidden;
    overflow-y: auto;
  }
  .list-item {
    margin-bottom: 6px;
  }
  .content {
    display: flex;
  }
  .content-img {
    width: 100px;
    height: 100px;
    background-color: #000;
  }
  .content-message {
    margin-left: 8px;
    padding: 0 10px 0 0;
    width: 253px;
    .text-o {
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
    }
    .text-n {
      font-size: 14px;
      color: #999999;
      margin: 8px 0;
    }
    .price {
      color: salmon;
      font-size: 16px;
    }
  }
  /deep/ .van-button--info {
    background: #fa8072;
  }
  /deep/ .van-nav-bar__text,
  /deep/ .van-icon {
    color: #fa8072;
  }
  /deep/ .van-address-item__edit {
    display: none;
  }
  /deep/ .van-button--danger {
    background-color: #fa8072;
    border-color: #fa8072;
  }
  .order-total {
    margin-top: 20px;
    border-bottom: 1px solid #e8e8e8;
  }
  .total-count {
    font-size: 12px;
    color: #999;
    height: 40px;
    line-height: 40px;
  }
  .total-price {
    font-weight: bold;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
  }

  .popup-box {
    min-height: 300px;
    max-width: 580px;
    overflow-y: auto;
  }

  .address {
    height: 40px;
    line-height: 40px;
    padding: 0 10px;
    background-color: #fff;
    border-radius: 10px;
    font-size: 14px;
  }
  .order-box {
    padding: 10px;
  }

  .order-content {
    margin-top: 20px;
  }
}
</style>